<!-- echartscom.vue -->
<template>
  <div class="charttest">
    <div :style="{width: '700px', height: '400px'}" :id="echarts" class="echarts" ref="echarts">
    </div>
  </div>
</template>

<script>
  import * as echarts from 'echarts';
  export default {
    name:'echarts2',
    data() {
      return {};
    },
    methods: {
      drawCharts() {
        let myChart = echarts.init(document.getElementById(this.echarts));
        myChart.setOption({
          title: {
            text: '访问比例统计图',
            subtext: '虚拟数据',
            left: 'left'
          },
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b}: {c} ({d}%)'
          },
          legend: {
            data: ['直接访问', '邮件营销', '联盟广告', '视频广告', '搜索引擎']
          },
          toolbox: {
            show: true,
            feature: {
                mark: {show: true},
                dataView: {show: true, readOnly: false},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
          series: [
            {
              name: '访问来源',
              type: 'pie',
              radius: ['50%', '70%'],
              avoidLabelOverlap: false,
              label: {
                normal: {
                  show: false,
                  position: 'center'
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '30',
                    fontWeight: 'bold'
                  }
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data: [
                { value: 335, name: '直接访问' },
                { value: 310, name: '邮件营销' },
                { value: 234, name: '联盟广告' },
                { value: 135, name: '视频广告' },
                { value: 1548, name: '搜索引擎' }
              ]
            }
          ]
        });
      }
    },
    computed: {
      echarts() {
        return 'echarts' + Math.random() * 100000;
      }
    },
    mounted() {
      this.drawCharts();
    },
  };
</script>

<style></style>