<template>
    <div id="box2" :style="{width: '700px', height: '400px'}"></div>
</template>

<script>
import * as echarts from 'echarts';
  export default {
    name:'echarts4',
    data() {
      return {};
    },
    methods: {
      drawCharts() {
        //初始化ehcharts实例
        var myChart=echarts.init(document.getElementById("box2"));
        //指定图表的配置项和数据
        var option = {
            title:{
                text:'月收入统计'
            },
              tooltip: {
                trigger: 'axis',
                axisPointer: {
                  type: 'cross',
                  crossStyle: {
                    color: '#999'
                  }
                }
              },
              toolbox: {
                    show: true,
                    feature: {
                        mark: {show: true},
                        dataView: {show: true, readOnly: false},
                        restore: {show: true},
                        saveAsImage: {show: true}
                    }
                },
              legend: {
                icon: 'rect',//形状  类型包括 circle，rect,line，roundRect，triangle，diamond，pin，arrow，none
                itemWidth: 10,  // 设置宽度
                itemHeight: 4, // 设置高度
                itemGap: 24, // 设置间距
                data: ['最高', '最低'],
                textStyle: {
                    //文字样式
                    color: '#c1dafc',
                    fontSize: '12'
                },
                right: '30%'
              },
              xAxis: [
                {
                  type: 'category',
                  data: ['第一日', '第二日', '第三日', '第四日', '第五日', '第六日', '第七日', '第八日', '第九日', '第十日'],
                  axisPointer: {
                    type: 'shadow'
                  }
                }
              ],
              yAxis: [
                {
                  type: 'value',
                  name: '单位：(￥)',
                  min: 0,
                  max: 50,
                  interval: 10,
                  axisLabel: {
                    formatter: '{value}'
                  }
                }
              ],
              series: [
                {
                  name: '最高',
                  type: 'bar',
                  barWidth: '5%',
                  data: [2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0]
                },
              {
                name: '最低',
                type: 'bar',
                barWidth: '5%',
                data: [2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8]
              }
              ],
              color: ['#b1de6a', '#4ab0ee']
            };
            //使用刚刚指定的配置项和数据项显示图表
            myChart.setOption(option);
      }
    },
    mounted() {
      this.drawCharts();
    },
  };
    
</script>